import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import algoliasearch from 'algoliasearch/lite';
import { InstantSearch, SearchBox, Hits, Highlight } from 'react-instantsearch';
import RecipeDetail from './RecipeDetail';
import './App.css';

const searchClient = algoliasearch('WA6MQG7XI2', '8dfd72cab9a5d5bbe956cae18ba9bc7d');

function Hit({ hit }) {
  return (
    <article>
      <h1>
        <Highlight attribute="recipe_name" hit={hit} /> (⭐{hit.rating})
      </h1>
      <p>Recipe Category: <Highlight attribute="cuisine_path" hit={hit} /></p>
      <img src={hit.img_src} width={300} height={250} alt="" />
      <p>Prep time: {hit.prep_time} </p>
      <p>Total time: {hit.total_time} </p>
      <a href={`/recipe/${encodeURIComponent(hit.objectID)}`}>View Details</a>
    </article>
  );
}

function renderHits({ hits }) {
  if (!hits) {
    return <div>No results found</div>;
  }

  if (!Array.isArray(hits)) {
    hits = [hits];
  }

  return (
    <div className="hits">
      {hits.map(hit => (
        <Hit key={hit.objectID} hit={hit} />
      ))}
    </div>
  );
}

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/recipe/:id" element={<RecipeDetail />} />
        <Route
          path="/"
          element={
            <InstantSearch searchClient={searchClient} indexName="Lorcan_Test" insights={true}>
              <div className="custom-search-box-container">
                <SearchBox />
                <Hits hitComponent={renderHits} />
              </div>
            </InstantSearch>
          }
        />
      </Routes>
    </Router>
  );
}

export default App;
